<template>
<div class="hot-new-wraper" :name="now">
  <MyHeader :head="title"></MyHeader>
  <ul class="swiper-container-hot" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
    <li v-for="circle in circles" :key="circle.id">
      <div class="top">
        <span><img :src="circle.photo" alt=""></span>
        <h2>{{circle.user.name}}</h2>
        <p>{{circle.content}}</p>
      </div>
      <figure v-if="circle.photoList"><img v-for="(item,index) in circle.photoList" :key="index" v-lazy="item" :src="item" v-image-preview alt=""></figure>
      <div class="hot-info">
        <label>{{circle.updateDate.substring(0,10)}}</label>
        <a><i><img src="../../static/images/circle_zan.png" alt=""></i>{{circle.laudCount}}</a>
      </div>
    </li>
  </ul>
  <p v-show="loading" class="page-infinite-loading">
    <img src="../../static/images/load.gif" alt=""> 加载中...
  </p>
  <MyFooter :name="now"></MyFooter>
</div>
</template>

<script>
export default {
  data () {
    return {
      now: 2,
      title: '圈子',
      circles: [],
      loading: false,
      pSize: 12,
      pNo: 1
    }
  },
  methods: {
    loadMore () {
      this.getCircle()
    },
    getCircle () {
      this.loading = true
      let params = {
        type: 1,
        isHot: 1,
        pageSize: this.pSize,
        pageNo: this.pNo
      }
      this.$axios.get('/api/a/ios/findCategoryWriteList', {params: params
        }).then(res => {this.circles = this.circles.concat(res.data.data)
            ++this.pNo
          if (res.data.data.length < this.pSize) {
            this.loading = true
          } else {
            this.loading = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>



