<template>
<div class="denglu-wrapper">
  <MyHeader :head="title"></MyHeader>
  <ul class="input-box">
    <li><img src="../../static/images/user.png" alt=""><input type="text" placeholder="请输入用户名" v-model="userName"></li>
    <li><img src="../../static/images/pass.png" alt=""><input type="password" placeholder="请输入密码" v-model="passWord"></li>
  </ul>
  <button class="login" type="button" @click="partyLogin()">登录</button>
</div>
</template>

<script>
import { Toast } from '../../node_modules/mint-ui'
export default {
  data () {
    return {
      userName: '',
      passWord: '',
      title: '登录',
      msg: '登录失败'
    }
  },
  methods: {
    openToast () {
      Toast(this.msg)
    },
    partyLogin () {
      let params = {
        username: this.userName,
        password: this.passWord
      }
      this.$axios.post('/api/a/ios/login', {
        params: params
      }).then(res => {
        console.log(res)
        if (res.data.status == '0') {
          this.msg = '登录成功'
          setTimeout(() => {
            this.$router.push({
              path: '/mine'
            })
          }, 3000)
        } else {
          this.msg = '登录失败'
          this.openToast()
          this.$router.push({
            path: '/mine'
          })
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>



