<template>
<div>
  <MyHeader :head="detailContent.title"></MyHeader>
  <div class="content-detail">
    <h1>{{detailContent.title}}</h1>
    <h2>发表时间: {{detailContent.createDate}}</h2>
    <div v-html="detail.content"></div>
  </div>
</div>
</template>

<script>
export default {
  name: 'detail',
  data () {
    return {
      detailContent: '',
      detail: ''
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      let self = this
      let id = self.$route.params.detailId
      let params = {
        'id': id
      }
      self.$axios.get('/api/a/ios/menuDetail', {
        params: params
      }).then(function (res) {
        self.detailContent = res.data.data
        self.items = res.data.data.newsArticle
        self.detail = res.data.data.articleData
      }).catch(function (err) {
        console.log(err)
      })
    })
  }
}
</script>



