<template>
<ul class="party_news_list">
  <li v-for="(item,key) in items" :key="key" v-if="item.modelType==0">
    <div class="news_title">
      <img src="../../static/images/news_icon.jpg" alt="">
      <div class="title-info">
        <h1>{{item.office.name}}</h1>
        <p>{{item.createDate}}</p>
      </div>
    </div>
    <div class="news_main clear-float">
      <router-link :to="'/detail/'+item.id">
        <h3 class="text-hidden-lines">{{item.title}}</h3>
      </router-link>
    </div>
    <a href="" class="look_more">查看更多</a>
  </li>
  <li v-for="(item,key) in items" :key="key" v-if="item.modelType==1">
    <div class="news_title">
      <img src="../../static/images/news_icon.jpg" alt="">
      <div class="title-info">
        <h1>{{item.office.name}}</h1>
        <p>{{item.createDate}}</p>
      </div>
    </div>
    <div class="news_main clear-float">
      <router-link :to="'/detail/'+item.id">
        <figure><img v-bind:src="item.image" alt=""></figure>
        <h2 class="text-hidden-lines">{{item.title}}</h2>
      </router-link>
    </div>
    <a href="" class="look_more">查看更多</a>
  </li>
</ul>
</template>

<script>
export default {
  data () {
    return {
      items: []
    }
  },
  mounted () {
    this.$axios.get('/api/a/ios/partyHomePage').then(res => {
      this.items = res.data.data.newsArticle
    }).catch(err => {
      console.log(err)
    })
  }
}
</script>



