import App from '../App'
import Vue from 'vue'
import main from '@/components/mainpage'
import activitypage from '@/components/activity'
import detail from '@/components/detail'
import activityDetail from '@/components/activityDetail'
import circle from '@/components/PartyCircle'
import mine from '@/components/PartyMine'
import login from '@/components/PartyLogin'
import chart from '@/components/PartyChart'
import list from '@/components/PartyList'

// 自定义组件
import MyHeader from '@/components/MyHeader.vue'
import MyFooter from '@/components/MyFooter.vue'
import MyBanner from '@/components/MyBanner.vue'
import MyFunction from '@/components/MyFunction.vue'
import ListItem from '@/components/ListItem.vue'

Vue.component('MyHeader', MyHeader)
Vue.component('MyFooter', MyFooter)
Vue.component('MyBanner', MyBanner)
Vue.component('MyFunction', MyFunction)
Vue.component('ListItem', ListItem)

let routes = [
  {
    path: '',
    redirect: '/main',
    component: App,
    children: [
      {
        path: '/main',
        component: main,
        name: '主页'
      },
      {
        name: 'detail',
        path: 'detail/:detailId',
        component: detail
      },
      {
        path: '/activity',
        name: 'activitypage',
        component: activitypage
      },
      {
        path: '/circle',
        name: 'circle',
        component: circle
      },
      {
        name: 'activityDetail',
        path: '/activityDetail',
        component: activityDetail
      },
      {
        path: '/mine',
        name: 'mine',
        component: mine
      },
      {
        path: '/login',
        name: 'login',
        component: login
      },
      {
        path: '/chart',
        name: 'chart',
        component: chart
      },
      {
        path: '/list',
        name: 'list',
        component: list
      }
    ]
  }

]

export default routes



