<template>
<div v-swiper:mySwiper="swiperOption">
  <div class="swiper-wrapper">
    <div class="swiper-slide" v-for="banner in banners" v-bind:key="banner.index">
      <img :src="'https://dangjian.suitaquba.com' + banner.img">
    </div>
  </div>
  <div class="swiper-pagination swiper-pagination-bullets"></div>
</div>
</template>

<script>
export default {
  props: ['banners'],
  data () {
    return {
      swiperOption: {
        autoplay: 5000,
        loop: true,
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  }
}
</script>



