<template>
<div class="main">
  <MyBanner :banners="banners"></MyBanner>
  <MyFunction :category="category"></MyFunction>
  <ListItem></ListItem>
  <MyFooter :name="now"></MyFooter>
</div>
</template>

<script>
export default {
  data () {
    return {
      items: '',
      banners: '',
      category: '',
      now: 0
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      let self = this
      self.$axios.get('/api/a/ios/partyHomePage')
        .then(function (res) {
          self.banners = res.data.data.bannerList
          self.category = res.data.data.functionCategoryList
        })
        .catch(function (err) {
          console.log(err)
        })
    })
  }
}
</script>



