<template>
<div class="mine_wrapper">

  <div class="login-top">
    <div class="img-box"><img src="../../static/images/icon_head.png" alt=""></div>
    <router-link to="/login"><p class="text-center">立即登录</p></router-link>
  </div>
  <ul class="function-list">
    <li><a href="">
          <img src="../../static/images/ic_certification.png" alt="">
          所属支部
          <span class="float__right"><img src="../../static/images/arrow_right.png" alt=""></span>
        </a></li>
    <li><a href="">
          <img src="../../static/images/ic_opinion.png" alt="">
          意见反馈
          <span class="float__right"><img src="../../static/images/arrow_right.png" alt=""></span>
        </a></li>
    <li><a href="note-taking.html">
          <img src="../../static/images/ic_mine_score.png" alt="">
          我的学习积分
          <span class="float__right"><img src="../../static/images/arrow_right.png" alt=""></span>
        </a></li>
    <li><a href="">
          <img src="../../static/images/ic_mine_note.png" alt="">
          我的随手记
          <span class="float__right"><img src="../../static/images/arrow_right.png" alt=""></span>
        </a></li>
    <li><a href="">
          <img src="../../static/images/ic_mine_about.png" alt="">
          关于我们
          <span class="float__right"><img src="../../static/images/arrow_right.png" alt=""></span>
        </a></li>
  </ul>
  <MyFooter :name="now"></MyFooter>
</div>
</template>

<script>
export default {
  data () {
    return {
      mineInfo: '',
      now: 3
    }
  },
  mounted() {
  },
  methods: {
    getInfo () {
      this.$axios.get('/api/a/ios/info').then(res => {
         this.mineInfo = res.data.data
      }).catch (err => {
        console.log(err)
      })
    }
  }
}
</script>



