<template>
<ul class="tab-function clear-float">
  <li v-for="item in category" v-bind:key="item.index" @click="jump(item.id)">
    <img :src="'https://dangjian.suitaquba.com' + item.image" alt="">
    <p>{{item.name}}</p>
  </li>
</ul>
</template>

<script>
export default {
  props: ['category'],
  methods: {
    jump(id) {
      if (id == '1b40c39fb568476d919bf7a9f475084f') {
        this.$router.push({
          path: '/chart'
        })
      } else {
        this.$router.push({
          path: '/list'
        })
      }
    }
  }
}
</script>



