<template>
<div>
  <div class="marBottom"></div>
  <ul class="foot-nav">
    <li :class="{now:name==0}">
      <router-link to="/">
        <img src="../../static/images/ic_home_enabled_off.png" alt="" v-if="name==0">
        <img src="../../static/images/ic_home_enabled_on.png" alt="" v-else>
        <p>首页</p>
      </router-link>
    </li>
    <li :class="{now:name==1}">
      <router-link to="activity">
        <img src="../../static/images/ic_special_enabled_off.png" alt="" v-if="name==1">
        <img src="../../static/images/ic_special_enabled_on.png" alt="" v-else>
        <p>专题</p>
      </router-link>
    </li>
    <li :class="{now:name==2}">
      <router-link to="circle">
        <img src="../../static/images/ic_friendscircle_enabled_on.png" alt="" v-if="name==2">
        <img src="../../static/images/ic_friendscircle_enabled_off.png" alt="" v-else>
        <p>圈子</p>
      </router-link>
    </li>
    <li :class="{now:name==3}">
      <router-link to="/mine">
        <img src="../../static/images/ic_mine_enabled_off.png" alt="" v-if="name==3">
        <img src="../../static/images/ic_mine_enabled_on.png" alt="" v-else>
        <p>我的</p>
      </router-link>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  props: ['name']
}
</script>



