
<template>
<div class="main page-infinite">
  <MyHeader :head="title"></MyHeader>
  <div>
    <ul class="activity-wrapper">
      <li v-for="item in activity" v-bind:key="item.id">
        <router-link :to="{path:'/activityDetail',query:{id:item.id}}">
          <img v-if="item.images.substring(0,4) == 'http'" :src="item.images">
          <img v-else-if="item.images.substring(0,4) != 'http'" :src="'https://dangjian.suitaquba.com' + item.images.replace('|','')" alt="">
          <p class="text-hidden-line">{{item.title}}</p>
        </router-link>
      </li>
    </ul>
  </div>
  <MyFooter :name="now"></MyFooter>
</div>
</template>

<script>
export default {
  data () {
    return {
      activity: [],
      now: 1,
      title: '专题'
    }
  },
  mounted: function () {
    this.dataList()
  },
  methods: {
    dataList () {
      let self = this
      self.$axios.get('/api/a/ios/getSubjectList').then(function (res) {
        self.activity = res.data.data
      }).catch(function (err) {
        console.log(err)
      })
    }
  }
}
</script>



