<template>
<div>
  <div class="header">
    <b class="back" @click="back"><img src="../../static/images/back_icon.png" alt=""></b>
    <p class="text-hidden-line">{{head}}</p>
  </div>
  <div class="mar-top"></div>
</div>
</template>

<script>
export default {
  props: ['head'],
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}
</script>



